.fixed-list {
  z-index: 100;
  text-decoration: none;
  position: fixed; /**/
  display: flex; /*^*/
  flex-direction: column;
  justify-content: center; /*^*/
  align-items: center; /*^*/
  right: 16px;
  bottom: 50%;
  a {
    transition: 1s;
    &:hover {
      transform: scale(1.2);
    }
  }
}
