//  input-placeholder 
::-webkit-input-placeholder {
  font-size: 16px;
  line-height: 1.5;
  color: $secondary !important;
  @include small {
    font-size: 14px;
  }
}
//  hover 
.hover-secondary {
  &:hover {
    color: $secondary;
  }
}
.hover-light {
  color: $light;
  &:hover {
    color: $success;
  }
}
// Xscrollbar
.no-scrollbar {
  overflow-x: auto;
  &::-webkit-scrollbar {
    display: none;
  }
}
// active 
.active {
  border: 4px solid $secondary;
}
// 
.cursor-pointer {
  cursor: pointer;
}
// position-absolute 4px  n 
.top-n4 {
  top: -16px;
}
.bottom-6 {
  bottom: 24px;
}
.start-9 {
  left: 36px;
}
.end-9 {
  right: 36px;
}
